html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

/* Slider */
.slick-slider {
  position: relative;
  display: block;
  box-sizing: border-box;
  -moz-box-sizing: border-box;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -ms-touch-action: pan-y;
  touch-action: pan-y;
  -webkit-tap-highlight-color: transparent;
}

.slick-list {
  position: relative;
  overflow: hidden;
  display: block;
  margin: 0;
  padding: 0;
}

.slick-list:focus {
  outline: none;
}

.slick-list.dragging {
  cursor: pointer;
  cursor: hand;
}

.slick-slider .slick-track,
.slick-slider .slick-list {
  -webkit-transform: translate3d(0, 0, 0);
  -moz-transform: translate3d(0, 0, 0);
  -ms-transform: translate3d(0, 0, 0);
  -o-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
}

.slick-track {
  position: relative;
  left: 0;
  top: 0;
  display: block;
}

.slick-track:before, .slick-track:after {
  content: "";
  display: table;
}

.slick-track:after {
  clear: both;
}

.slick-loading .slick-track {
  visibility: hidden;
}

.slick-slide {
  float: left;
  height: 100%;
  min-height: 1px;
  display: none;
}

[dir="rtl"] .slick-slide {
  float: right;
}

.slick-slide img {
  display: block;
}

.slick-slide.slick-loading img {
  display: none;
}

.slick-slide.dragging img {
  pointer-events: none;
}

.slick-initialized .slick-slide {
  display: block;
}

.slick-loading .slick-slide {
  visibility: hidden;
}

.slick-vertical .slick-slide {
  display: block;
  height: auto;
  border: 1px solid transparent;
}

body {
  background: url("../img/bg.jpg") center center no-repeat;
  background-size: cover;
}

.out {
  padding-top: 20px;
  padding-bottom: 20px;
}

.pics {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin-top: 20px;
  margin-bottom: 20px;
}

.pics__product {
  flex-basis: 155px;
}

.pics__signs {
  flex-basis: 155px;
}

@media only screen and (max-width: 360px) {
  .pics {
    margin-top: 5px;
    margin-bottom: 5px;
  }
}

.varyforte, .guarantee, .natural {
  margin: 0 auto;
}

.varyforte {
  background: url("../img/img.png") no-repeat 0 -2px;
  width: 124px;
  height: 318px;
}

.guarantee {
  background: url("../img/img.png") no-repeat -181px 0;
  width: 161px;
  height: 149px;
}

.natural {
  background: url("../img/img.png") no-repeat -187px -174px;
  width: 155px;
  height: 148px;
}

.banner {
  margin-top: 20px;
  margin-bottom: 20px;
  padding: 10px;
  background-color: rgba(51, 67, 41, 0.8);
  font-family: 'Acme', sans-serif;
  font-weight: normal;
  text-align: center;
}

.banner__txt {
  font-size: 48px;
  color: #fff;
}

.banner__txt--desc {
  font-size: 28px;
  color: #fcf49e;
}

@media only screen and (max-width: 414px) {
  .banner {
    margin-top: 15px;
    margin-bottom: 15px;
  }
  .banner__txt {
    font-size: 38px;
  }
  .banner__txt--desc {
    font-size: 20px;
  }
}

@media only screen and (max-width: 375px) {
  .banner {
    margin-top: 10px;
    margin-bottom: 10px;
  }
  .banner__txt {
    font-size: 30px;
  }
  .banner__txt--desc {
    font-size: 18px;
  }
}

@media only screen and (max-width: 360px) {
  .banner {
    margin-top: 5px;
    margin-bottom: 5px;
  }
  .banner__txt {
    font-size: 24px;
  }
  .banner__txt--desc {
    font-size: 14px;
  }
}

.ask-btn {
  display: block;
  margin: 50px auto;
  width: 300px;
  height: 80px;
  border: 6px solid #34442a;
  border-radius: 12px;
  background-clip: padding-box;
  background-color: #fcf49e;
  font-family: "Acme", sans-serif;
  font-size: 48px;
  font-weight: normal;
  color: #34442a;
  line-height: 68px;
  text-align: center;
  text-decoration: none;
}

.ask-btn:hover {
  background-color: rgba(52, 68, 42, 0.3);
}

@media only screen and (max-width: 414px) {
  .ask-btn {
    margin: 0 auto;
    font-size: 36px;
    height: 72px;
    line-height: 60px;
  }
}
